import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import $ from 'jquery';
import DOMPurify from 'dompurify';
import './App.css';
import twitter from './twitter.jpg'


class Quote extends React.Component {
	constructor(props) {
    super(props);
    this.state = {
      quote: '',
      author: ''
    };
    this.handleClick = this.handleClick.bind(this);
    this.getQuote = this.getQuote.bind(this);
  }

  getQuote() {
  $.ajax( {
  	url: 'https://quotesondesign.com/wp-json/posts?filter[orderby]=rand&filter[posts_per_page]=1',
  	success: (data, err) => {
  		let post = data[0];
  		let author = post.title;
  		let content = post.content;
  		this.setState({
  			quote: content,
  			author: author
  		});
  		},
  	cache: false
  });
};

  componentWillMount() {
    this.getQuote();
  }

  handleClick() {
    this.getQuote();
  }
  
  render() {
  	let quote = this.state.quote;
  	let author = this.state.author;
    let tweet = `http://twitter.com/home?status=${quote}`
    return (
      <div className="text" id="quote-box">
      <div id="text" dangerouslySetInnerHTML={{__html: DOMPurify.sanitize(quote)}}></div><div id="author" dangerouslySetInnerHTML={{__html: DOMPurify.sanitize(author)}}></div>
      <br/><button type="button" id="new-quote" className="btn btn-primary" onClick={this.handleClick}>Get a new quote</button> 
      <a href={tweet} target="_blank" id="tweet-quote"><img src={twitter} /></a></div>
      );
  }
}


ReactDOM.render(<App />, document.getElementById('root'));

export { Quote };



